.select-overlay {
  top: 0;
  left: 0;
  text-align: center;
  position: fixed;
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;

  .select-overlay-outer {
    top: 0;
    position: absolute;
    z-index: 3;
    background-color: black;
    opacity: 0.8;
    width: 100%;
    height: 100%;
  }
  .select-overlay-inner {
    align-self: center;
    z-index: 4;

    .value {
      cursor: pointer;
      color: $color-base;
      background-color: $color-bg;
      border: 1px solid $color-base;
      width: 80vw;
      font-size: 2rem;
      line-height: 10vh;
      -webkit-transition-duration: 0.4s; /* Safari */
      transition-duration: 0.4s;

      &:first-child {
        border-radius: 1rem 1rem 0 0;
      }

      &:last-child {
        border-radius: 0 0 1rem 1rem;
      }

      &:hover:not(.no-hover) {
        color: $color-bg;
        background-color: $color-base;
      }
    }
  }
}
