.players {
  padding: 1rem;

  button.add-player {
    @include sit-green-button;
  }

  .registered-players {
    margin-bottom: 3rem;
  }

  table,
  th,
  td {
    border-right: 0;
    border-left: 0;
  }
  tr.player-row {
    padding: 1rem 0;

    .player-pos {
      .player-move-up {
        margin: 0 1rem;
        svg {
          transform: rotate(-90deg);
        }
      }
      .player-current-pos {
        font-size: 1.5rem;
      }
      .player-move-down {
        margin: 0 1rem;
        svg {
          transform: rotate(90deg);
        }
      }
    }
  }
}