table {
  border-spacing: 5px;
  width: 100%;
  border-right: 1px solid #9d9d9d;
  border-left: 1px solid #9d9d9d;
  border-collapse: collapse;

  th,
  td {
    border-right: 1px solid #9d9d9d;
    border-left: 1px solid #9d9d9d;
    border-collapse: collapse;
    min-width: 3em;
    text-align: center;
    border-bottom: 1px solid #ddd;
    margin: 0;
    padding: 0;
    input {
      width: 4em;
      padding: 0.5em;
    }
  }
  th {
    height: 50px;
    background-color: $color-base;
    color: $color-bg;
  }
  td {
    height: 3em;
    vertical-align: middle;
  }
  tr {
    &:nth-child(even) {
      background-color: lighten($color-bg, 10%);
    }
  }
}

table {
  border-spacing: 5px;
  width: 100%;
}
