.quixx {
  &__grid {
    margin: 4px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 4px;
    justify-items: center;

    &__field {
      color: #000;
      font-weight: bold;
      height: 2em;
      width: 100%;
      display: flex;
      justify-content: space-around;
      line-height: 2em;
      align-content: center;
      box-sizing: border-box;
      border: 2px solid #fff;
      border-radius: 0.5em;
      cursor: pointer;
      position: relative;

      &--closed:before {
        content: ' ';
        position: absolute;
        width: 100%;
        border-top: 4px solid #fff;
        margin-top: 0.9em;
        padding: 0 2%;
      }

      &--crossed {
        position: absolute;
        color: #fff;
        text-align: center;
      }
    }
  }

  &__info {
    display: flex;
    justify-content: space-between;
    margin: 1em;

    &__bad-throws {
      display: grid;
      grid-template-columns: max-content repeat(3, 1fr);
      grid-gap: 4px;
      > div {
        align-self: center;
        padding-right: 1em;
      }
      > span {
        padding: 0.25em 0.5em;
        height: 1em;
        width: 0.75em;
        color: #000;
        background-color: #fff;
        border-radius: 0.25em;
        cursor: pointer;
      }
    }

    &__sum {
      display: flex;

      > div {
        align-self: center;
        padding-right: 0.5em;
      }
      > span {
        padding: 0.25em;
        clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
        background-color: #b59702;
        color: #000;
      }
    }
  }
}