.gimmick {
  height: 100%;
  overflow: auto;

  .form-control {
    position: relative;
    input {
      font-size: 1.5em;
      width: 90%;
      background-color: transparent;
      border: transparent;
      color: $color-base;
      outline: 0;
      text-align: center;
    }
    .invalid-feedback {
      position: absolute;
      color: red;
      display: block;
      font-size: 0.5em;
      left: 1.25em;
      bottom: -1.25em;
    }
  }

  table {
    th.active {
      position: relative;
      background-color: transparent;

      &:before {
        content: ' ';
        left: 0;
        top: 0;
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;
        background: #4CAF50;
      }
    }

    th.winner {
      background: url('/fireworks.gif');
      background-size: cover;


      span {
        background-color: $color-base;
        padding: 0.25em 0.5em;
        border-radius: 0.5em;
      }
    }
  }

  tbody td.inactive {
    position: relative;

    &:after {
      content: ' ';
      left: 0;
      top: 0;
      position: absolute;
      width: 100%;
      height: 100%;
      background: black;
      opacity: 0.5;
    }
  }
}