@mixin sit-green-button {
  outline: 0;
  background-color: #4caf50; /* Green */
  border: 2px solid #4caf50; /* Green */
  border-radius: 10px;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  &:hover {
    background-color: white;
    color: black;
  }
}

.spade-is-trump {
  height: 100%;
  overflow: scroll;

  .configs {
    padding: 1rem;
  }
  table {
    td {
      &.active {
        box-shadow: 0 5px 16px 5px rgba(0, 0, 255, 0.5), 0 -5px 20px 5px rgba(0, 0, 255, 0.5);
        border-radius: 10px;

        .points-success {
          border-radius: 0 0 0 5px;
        }
        .points-failed {
          border-radius: 0 0 5px 0;
        }
      }
      .error-round {
        color: red;
        padding-left: 5px;
      }
      .winner {
        color: darkgoldenrod;
        padding-left: 5px;
      }
    }

    tr {
      &.disabled {
        opacity: 0.5;
      }
      &:nth-child(even) {
        background-color: lighten($color-bg, 10%);
      }
      &.sum {
        background-color: #3b4e3b;
      }
    }
  }

  form.startup {
    margin-bottom: 3rem;
    .form-field {
      padding-bottom: 1em;
    }
    input {
      width: 100%;
      padding: 12px 20px;
      margin: 8px 0;
      outline: 0;
      box-sizing: border-box;
      &.is-invalid {
        border: 2px solid red;
      }
      border-radius: 10px;
      background-color: #f4f4f4;
      color: black;
      font-size: 2em;
      &:focus {
        border: 3px solid #555;
        &.is-invalid {
          border: 2px solid red;
        }
      }
    }
    span.invalid-feedback {
      color: red;
    }
    button {
      @include sit-green-button;
      float: right;
    }
    @media (min-width: 480px) {
      .form-control {
        vertical-align: top;
        display: inline-block;
        width: calc(50% - 1em);
        margin-right: 1rem;
      }
    }
  }
  .points-success,
  .points-failed {
    width: 50%;
  }
  .points-tip {
    padding: 0.5em;
  }
  .points-lost {
    background-color: #dc0000;
  }
  .points-earned {
    background-color: #00a900;
  }
  .points-failed {
    background-color: red;
    border: 2px solid red;
    color: white;
    padding: 0.5em;
  }
  .points-success {
    background-color: darkgreen;
    border: 2px solid darkgreen;
    color: white;
    padding: 0.5em;
  }
}
