@import '../../node_modules/react-staged/dist/assets/staged.scss';

.App {
  height: 100%;

  .App-content {
    padding: 1rem;
    height: calc(100% - 2rem);

    .preview {
      background-color: lighten($color-bg, 5%);
      box-shadow: 0 0 4px 2px black;
      margin-top: 1rem;
      height: calc(25% - 1rem);
      position: relative;
      vertical-align: middle;

      &:last-child {
        margin-bottom: 1rem;
        height: calc(25% - 2rem);
      }

      a {
        display: flex;
        height: 100%;
        text-decoration: none;
      }

      h2 {
        padding: 0 1rem;
        align-self: center;
        color: $color-base;
        font-size: 2rem;
      }

      img {
        left: 0;
        width: 30%;
        object-fit: cover;
      }
    }
  }

  .App-logo {
    height: 1.5em;
    padding: 0.25em;
  }

  .App-header {
    position: absolute;
    z-index: 1;
    right: 0;
  }
}
