:root {
  --footer-height: 0px;
}

#root {
  height: calc(100% - var(--footer-height));
}

footer {
  position: fixed;
  z-index: 1;
  bottom: 0;
  width: 100vw;
  text-align: center;
  height: 3rem;

  &.top {
    bottom: unset;
    top: 0;
  }

  .footer-handle {
    position: absolute;
    z-index: 2;
    left: calc(50% - 2.5rem);
    width: 5rem;
    height: 5rem;
    border-radius: 5rem;
    bottom: -1rem;
    box-shadow: 0 0 4px 2px black;
    cursor: pointer;

    &.closed {
      bottom: -4rem;
    }
  }

  .footer-border {
    position: absolute;
    z-index: 1;
    height: 3rem;
    bottom: 0;
    width: 100vw;
    box-shadow: 0 0 4px 2px black;
  }

  nav {
    position: absolute;
    z-index: 3;
    bottom: 0;
    width: 100vw;
    background-color: lighten($color-bg, 5%);

    .footer-actions {
      position: relative;

      z-index: 2;
      display: flex;
      height: 3rem;
      justify-content: space-around;

      .footer-action {
        align-self: center;
        padding: 0.75rem;
        cursor: pointer;

        .small {
          line-height: 0.75rem;
          font-size: 0.75rem;
        }

        &.no-handler {
          cursor: default;
        }
      }
    }
  }

  svg.icon {
    width: unset;
    height: 1em;
  }
}
